import React from 'react';
import DialogTitle from '@mui/material/DialogTitle';
import Dialog from '@mui/material/Dialog';
import SettingsAccordeon from './SettingsAccordeon';
import { Button, DialogActions, IconButton, useMediaQuery } from '@mui/material';
import { useTheme } from '@mui/material/styles';

export const SettingDialog = (props)=> {
  const theme = useTheme();
  const matches = useMediaQuery(theme.breakpoints.down('sm'))
  const { onClose, open } = props;
  const handleClose = () => {
    onClose();
  };

  return (
    <Dialog fullWidth fullScreen={matches && true} onClose={handleClose} open={open} PaperProps={{style:{backgroundColor:'#151f27'}}}>
      <DialogTitle sx={{backgroundColor:'#092129', color:'white'}}>
        Paramtres
        <IconButton sx={{color:'white', position:'absolute',right:8, top:8}} onClick={props.onQuit} >X</IconButton>
      </DialogTitle>
      <SettingsAccordeon localSettings={props.localSettings} callbacks={props.callbacks}/>
      <DialogActions>
        <Button sx={{margin:'auto'}} color="success" variant='outlined' onClick={props.onClose}>Enregistrer</Button>
        <Button sx={{margin:'auto'}}  variant='outlined' onClick={props.onQuit}>Annuler</Button>
      </DialogActions>
    </Dialog>
  );
}
